.ui-kit-cards{
  display: flex;
  background: #F4F4F6;
  padding: 130px 120px;
  position: relative;
  width: 1440px;
  box-sizing: border-box;

  .ui-kit-cards__ico-logo{
    width: 48px;
    height: 48px;
    position:absolute;
    left: 31px;
    top: 30px;
  }

  .ui-kit-cards__blk{
    margin: 20px;

    &.ui-kit-cards__blk_3{
      width: 320px;
    }

    .form-search-num{
      position: relative;
      margin-bottom: 40px;
    }

    .form-reservation{
      margin-bottom: 40px;

      .form-reservation__block{

        p{

          a{
            margin: 0 0 0 -4px;
          }
        }

        h2{

          &:last-child{
            margin-right: -9px;
          }
        }
      }
    }

    .form-reservation__block-prices{

      .form-reservation__icon-info-price{
        position: absolute;
        left: 211px;
      }
    }

    .ui-kit-cards__calendar-block{
      height: 395px;

      .calendar-block{
        margin-top: 0;
          
        .calendar{
          margin-left: 0;
          width: 320px;
          box-shadow: 0px 20px 20px rgba(31, 32, 65, 0.05);
        }
      }
    }

    .card-room{
      margin: 14px 2.5px 40px 0px;

      .link_lux{
        margin: 0 0 0 -4px;
      }
    }
  }

  .form-search-num,
  .form-registration,
  .form-reservation,
  .form-sign-in{
    margin: 0;
  }
}